import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, ToggleControl, RangeControl, __experimentalUnitControl as UnitControl, Dashicon } from '@wordpress/components';
import SelectPure from 'select-pure';

// Settings Components
import Title from '../../Components/Title';
import BDevice from '../../Components/BDevice';
import Background from '../../Components/Background';
import Typography from '../../Components/Typography';
import SelectPureControl from '../../Components/SelectPureControl';
import BColor from '../../Components/BColor';
import ColorsControl from '../../Components/ColorsControl';
import BorderControl from '../../Components/BorderControl';
import ShadowControl from '../../Components/ShadowControl';

import { addArrItem, removeArrItem, filterSelected, tabController } from './Const/functions';
import options from './Const/options';
const { generalStyleTabs, pxUnit, perUnit, emUnit } = options;

const Settings = ({ attributes, setAttributes, allCategories }) => {
	const { productsPerPage, stockStatus, selectedCategories, columns, columnGap, rowGap, isImage, isTitle, isRating, isPrice, isAddToCartBtn, textAlign, productBG, productBorder, productShadow, titleTypo, titleColor, ratingColor, priceTypo, priceColor, addToCartTypo, addToCartColors, onSaleColors } = attributes;

	const [device, setDevice] = useState('desktop');

	const onStockFilterChange = (item, val) => {
		setAttributes({
			stockStatus: val ? [...addArrItem(stockStatus, item)] : [...removeArrItem(stockStatus, item)]
		});
	}

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs} onSelect={() => tabController()}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'popular-products')} initialOpen={false}>
						{/* <div className='helpItem'>
							<a href='https://bblockswp.com/woocommerce-popular-products/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'popular-products')}</a>
						</div> */}

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/popular-products-block/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'popular-products')}</span>
								<span>{__('We are new and we need your help to grow!', 'popular-products')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Query', 'popular-products')}>
						<Title mt='0'>{__('Products Limit:', 'popular-products')}</Title>
						<RangeControl value={productsPerPage} onChange={val => setAttributes({ productsPerPage: val })} min={1} max={60} step={1} />

						<hr />

						<h3>Filter by Stock</h3>
						<ToggleControl label={__('Show In Stock Products', 'popular-products')} checked={stockStatus.includes('instock')} onChange={val => onStockFilterChange('instock', val)} />

						<ToggleControl className='mt10' label={__('Show Out of Stock Products', 'popular-products')} checked={stockStatus.includes('outofstock')} onChange={val => onStockFilterChange('outofstock', val)} />

						<ToggleControl className='mt10' label={__('Show In On Backorder Products', 'popular-products')} checked={stockStatus.includes('onbackorder')} onChange={val => onStockFilterChange('onbackorder', val)} />

						<hr />

						<h3>Filter by Category</h3>
						<Title mt='0'>{__('Select Categories:', 'advanced-post-block')}</Title>
						<SelectPureControl
							value={filterSelected(allCategories, selectedCategories)?.map(cat => cat)}
							onChange={val => setAttributes({ selectedCategories: val.map(cat => cat) })}
							options={allCategories?.map(cat => ({ label: `${cat.name} (${cat.count})`, value: cat.slug }))}
							SelectPure={SelectPure}
						/>
					</PanelBody>

					<PanelBody className='bPlPanelBody' title={__('Layout', 'popular-products')} initialOpen={false}>
						<PanelRow>
							<Title mt='0'>{__('Columns:', 'popular-products')}</Title>
							<BDevice device={device} onChange={val => setDevice(val)} />
						</PanelRow>
						<RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' withInputField={false} />

						<UnitControl className='mt20' label={__('Column Gap:', 'popular-products')} labelPosition='left' value={columnGap} onChange={val => setAttributes({ columnGap: val })} units={[pxUnit(30), perUnit(3), emUnit(2)]} isResetValueOnUnitChange={true} />

						<UnitControl className='mt20' label={__('Row Gap:', 'popular-products')} labelPosition='left' value={rowGap} onChange={val => setAttributes({ rowGap: val })} units={[pxUnit(40), perUnit(3), emUnit(2.5)]} isResetValueOnUnitChange={true} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Elements', 'popular-products')} initialOpen={false}>
						<ToggleControl label={__('Show Image', 'popular-products')} checked={isImage} onChange={val => setAttributes({ isImage: val })} />

						<ToggleControl className='mt10' label={__('Show Title', 'popular-products')} checked={isTitle} onChange={val => setAttributes({ isTitle: val })} />

						<ToggleControl className='mt10' label={__('Show Rating', 'popular-products')} checked={isRating} onChange={val => setAttributes({ isRating: val })} />

						<ToggleControl className='mt10' label={__('Show Price', 'popular-products')} checked={isPrice} onChange={val => setAttributes({ isPrice: val })} />

						<ToggleControl className='mt10' label={__('Show Add To Cart Button', 'popular-products')} checked={isAddToCartBtn} onChange={val => setAttributes({ isAddToCartBtn: val })} />
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Product', 'popular-products')}>
						<Background label={__('Background:', 'popular-products')} background={productBG} onChange={val => setAttributes({ productBG: val })} />

						<BorderControl label={__('Border:', 'popular-products')} border={productBorder} onChange={val => setAttributes({ productBorder: val })} />

						<ShadowControl label={__('Shadow:', 'popular-products')} shadow={productShadow} onChange={val => setAttributes({ productShadow: val })} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Product Title', 'popular-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'popular-products')} typography={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: 22 }} />

						<BColor label={__('Color:', 'popular-products')} value={titleColor} onChange={val => setAttributes({ titleColor: val })} defaultColor='#333' />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Product Rating', 'popular-products')} initialOpen={false}>
						<BColor label={__('Color:', 'popular-products')} value={ratingColor} onChange={val => setAttributes({ ratingColor: val })} defaultColor='#ffdf00' />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Product Price', 'popular-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'popular-products')} typography={priceTypo} onChange={val => setAttributes({ priceTypo: val })} defaults={{ fontSize: 14, fontWeight: 700 }} />

						<BColor label={__('Color:', 'popular-products')} value={priceColor} onChange={val => setAttributes({ priceColor: val })} defaultColor='#222' />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Product Add To Cart Button', 'popular-products')} initialOpen={false}>
						<Typography label={__('Typography:', 'popular-products')} typography={addToCartTypo} onChange={val => setAttributes({ addToCartTypo: val })} defaults={{ fontSize: 15, fontWeight: 600 }} />

						<ColorsControl colors={addToCartColors} onChange={val => setAttributes({ addToCartColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('On Sale Tag', 'popular-products')} initialOpen={false}>
						<ColorsControl colors={onSaleColors} onChange={val => setAttributes({ onSaleColors: val })} defaults={{ color: '#fff', bg: '#4527a4' }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;