import { registerBlockType, createBlock } from '@wordpress/blocks';

import metadata from '../block.json';
import Edit from './Edit';
import './editor.scss';
import icons from './Const/icons';

registerBlockType(metadata, {
	icon: icons.product,

	transforms: {
		from: [
			{ type: 'block', blocks: ['wrp/recent-products'], transform: attributes => createBlock('wpp/popular-products', attributes) },
			{ type: 'prefix', prefix: 'wpp', transform: () => createBlock('wpp/popular-products') }
		]
	},

	// Build in Functions
	edit: Edit,

	save: () => null
});